textarea,
input[type=text],
input[type=password],
input[type=search],
input[type=email],
input[type=url],
input[type=number],
input[type=tel],
input[type=date],
input[type=datetime],
input[type=datetime-local],
input[type=month],
input[type=time],
input[type=week] {
  border-radius: 4px !important;
  box-shadow: 0 2px 4px 0 #7D7C7C !important;

  &:focus {
    border: none !important;
    box-shadow: 0 2px 9px 0 #7D7C7C !important;
  }

}

.builder-contact-upload-field .control-label {
  padding: 10px;
  @include rounded-button($black, $white, $hover-effect: true);
  &:hover {
    cursor: pointer !important;
  }
}



input[type=file] {
  display: none;
}

.module-contact .required {
  position: relative;
  top: 44px;
  left: -4px;

  &::after {
    content: '00';
    font-size: 10px;
    border-radius: 50%;
    background: red;
    position: relative;
    right: 10px;
    bottom: 5px;
  }
}


